.casas-page{background-color:#fff}.casas-hero{position:relative;min-height:500px;background:linear-gradient(135deg, #3B6F7F 0%, rgb(34.7338709677, 65.3467741935, 74.7661290323) 100%);display:flex;align-items:center;justify-content:center;padding:3rem 0;overflow:hidden}.casas-hero__overlay{position:absolute;top:0;left:0;right:0;bottom:0;background:rgba(0,0,0,.3);z-index:1}.casas-hero__container{position:relative;z-index:2;text-align:center;color:#fff}.casas-hero__title{font-family:"Outfit",sans-serif;font-size:clamp(28px,5vw,48px);font-weight:700;color:#fff;margin-bottom:1rem;text-shadow:2px 2px 4px rgba(0,0,0,.3)}.casas-hero__subtitle{font-family:"Hind",sans-serif;font-size:clamp(18px,2.5vw,24px);font-weight:400;color:#fff;margin-bottom:2rem;text-shadow:1px 1px 3px rgba(0,0,0,.3)}.casas-hero__cta{font-family:"Outfit",sans-serif;font-weight:600;padding:.5rem 2rem;font-size:clamp(14px,1.5vw,18px)}@media(max-width: 768px){.casas-hero{min-height:400px;padding:2rem 0}}@media(max-width: 576px){.casas-hero{min-height:350px;padding:1.5rem 0}}.casas-section{padding:3rem 0;background-color:#fff}.casas-section--gray{background-color:#f5f5f5}.casas-section__title{font-family:"Outfit",sans-serif;font-size:clamp(24px,4vw,40px);font-weight:700;color:#000;text-align:center;margin-bottom:2rem}.casas-section__description{font-family:"Hind",sans-serif;font-size:clamp(14px,1.5vw,18px);color:#333;text-align:center;max-width:800px;margin:0 auto 1.5rem auto}@media(max-width: 768px){.casas-section{padding:2rem 0}}@media(max-width: 576px){.casas-section{padding:1.5rem 0}}.casas-card{background-color:#fff;border-radius:8px;padding:1.5rem;height:100%;box-shadow:0 2px 8px rgba(0,0,0,.1);transition:transform .3s ease,box-shadow .3s ease}.casas-card:hover{transform:translateY(-4px);box-shadow:0 4px 16px rgba(0,0,0,.15)}.casas-card--featured{border:2px solid #3b6f7f}.casas-card--success{background-color:rgba(59,111,127,.05);border-left:4px solid #3b6f7f}.casas-card__title{font-family:"Outfit",sans-serif;font-size:clamp(18px,2.5vw,24px);font-weight:600;color:#000;margin-bottom:.5rem}.casas-card__text{font-family:"Hind",sans-serif;font-size:clamp(14px,1.5vw,18px);color:#333;line-height:1.6;margin-bottom:.5rem}.casas-card__spec{font-family:"Hind",sans-serif;font-size:clamp(14px,1.5vw,18px);font-weight:600;color:#3b6f7f;margin-bottom:.5rem}.casas-card__price{font-family:"Outfit",sans-serif;font-size:clamp(20px,3vw,32px);font-weight:700;color:#f16700;margin-bottom:.25rem}.casas-card__disclaimer{font-family:"Hind",sans-serif;font-size:clamp(12px,1.2vw,14px);color:#ccc;font-style:italic;display:block}.casas-card__result{font-family:"Hind",sans-serif;font-size:clamp(14px,1.5vw,18px);color:#333;padding-top:.5rem;border-top:1px solid #ccc;margin-top:.5rem}.casas-spec{background-color:#fff;border-radius:8px;padding:1rem;height:100%}.casas-spec__title{font-family:"Outfit",sans-serif;font-size:clamp(18px,2.5vw,24px);font-weight:600;color:#3b6f7f;margin-bottom:.5rem;padding-bottom:.25rem;border-bottom:2px solid #3b6f7f}.casas-spec__list{font-family:"Hind",sans-serif;font-size:clamp(14px,1.5vw,18px);color:#333;list-style:none;padding-left:0;margin:0}.casas-spec__list li{padding-left:1rem;position:relative;margin-bottom:.25rem}.casas-spec__list li:before{content:"✓";position:absolute;left:0;color:#3b6f7f;font-weight:700}.casas-spec__list li:last-child{margin-bottom:0}.casas-process{text-align:center;padding:1rem}.casas-process__number{width:60px;height:60px;background:linear-gradient(135deg, #3B6F7F, rgb(34.7338709677, 65.3467741935, 74.7661290323));color:#fff;border-radius:50%;display:flex;align-items:center;justify-content:center;font-family:"Outfit",sans-serif;font-size:clamp(20px,3vw,32px);font-weight:700;margin:0 auto 1rem auto;box-shadow:0 4px 12px rgba(59,111,127,.3)}.casas-process__title{font-family:"Outfit",sans-serif;font-size:clamp(18px,2.5vw,24px);font-weight:600;color:#000;margin-bottom:.5rem}.casas-process__text{font-family:"Hind",sans-serif;font-size:clamp(1